import React from 'react';
import AgedPlotDays from "../../data/daily_shot_days";
import AgedDairyShots from "../../data/daily_shots";
import AgedTotalShots from "../../data/daily_total_shot";
import Loadable from "@loadable/component"

const LoadableChart = Loadable(() => import("../../node_modules/react-apexcharts/src/react-apexcharts"))

const SampleChart = () => {
    let options = {
        chart: {
            toolbar: {
                tools: {
                    download: false,
                    pan: false,
                }
            }
        },
        xaxis: {
            type: 'datetime',
            categories: AgedPlotDays
        },
        tooltip: {
            x: {
                format: 'dd MM yy HH mm'
            }
        },
    }
    let series = [
        {
            name: "接種回数",
            data: AgedDairyShots
        }
    ]

    return (
        <div>
            <p>
                合計接種回数: {AgedTotalShots.接種回数}
            </p>
            <LoadableChart
                options={options}
                series={series}
                type="bar"

            />
        </div>
    )
}

export default SampleChart